import React from 'react';
import { ReactComponent as Logo } from "../../assets/grad.svg";
import { basic } from "../../utils/Data";

export function AnimateRibbon( { logoSize, callback, animationLength } ) {
    
    React.useEffect(() => {
        setTimeout(() => callback(), animationLength * 1000)
    }, [])
    
    return (
        <div className={'layer type-5'}>
            <div className={'names'} style={{ animationDuration : animationLength + 's' }}>
                <span className={'heavy'}>{basic.name}</span> <br />
                <span className={'light'} style={{ animationDuration : animationLength + 's' }}>{basic.slogan}</span>
            </div>
            <div style={{ '--width' : logoSize + 'px', animationDuration : animationLength + 's' }}
                 className={'full-logo'}>
                <Logo />
            </div>
        </div>
    );
}

