import React from 'react';
import { GlassCard } from "./GlassCard";

export function WorkCard( { select, selected, item, i } ) {
    return <div
        className={'work-card' + (selected === i ? ' shown' : selected === -1 ? '' : ' hidden')}
        onClick={() => select(i)}
    >
        
        <GlassCard>
            <div className={'project-name'}>
                {item.name}
            </div>
            
            <div className={'project-info'}>
                <div className={'project-logo'}>
                {item.logo_type === 'text' ?
                    <span>{item.logo}</span> :
                    <img
                        src={item.logo}
                        alt={item.name}
                    />
                }
                </div>
    
                <div className={'project-description'}>
                    {item.description}
                </div>
            </div>
            
            <div className={'project-images'}>
                <img
                    className={'img1'}
                    src={item.img1}
                    alt={item.name}
                />
                {item.img2 ?
                    <img
                        className={'img2'}
                        src={item.img2}
                        alt={item.name}
                    /> : <></>
                }
            </div>
        </GlassCard>
    
    </div>
}
