import React from 'react';
import { HexagonIcon } from "./Hexagon";

export function ThemeButton( { currentTheme, changeThemeCallback } ) {
    return (
        <div className={'theme-button'} onClick={changeThemeCallback}>
            <HexagonIcon
                icon={[ 'sun', 'moon' ][currentTheme]}
            />
        </div>
    );
}

