const parallaxHexValues = {
    colors : [
        '#7100d3',
        '#d37400',
        '#71d300',
        '#00d390',
        '#d30074',
        '#0097d3',
    ],
    x : [
        '3%',
        '30%',
        '90%',
        '70%',
        '9%',
        '60%',
        '97%'
    ],
    size : [
        50,
        100,
        70,
        300,
        150,
    ]
}

export var hexes = [];

export function genHexes( N ) {
    let y = 90;
    for ( let i = 0; i < N; i++ ) {
        let hex = {};
        hex.x = parallaxHexValues.x[i % parallaxHexValues.x.length]
        hex.size = parallaxHexValues.size[i % parallaxHexValues.size.length]
        hex.color = parallaxHexValues.colors[i % parallaxHexValues.colors.length]
        hex.z = 5+Math.floor(Math.random() * 20);
        hex.y = y;
        y += hex.size * 1.5;
        hexes.push(hex);
    }
}

genHexes(40)
