import React from 'react';
import { education } from "../../utils/Data";
import { GlassCard } from "../Cards/GlassCard";
import { Section } from "./Section";

export function EducationSection( props ) {
    return (
        <Section {...props} >
            {education.map(( item, i ) => {
                return <React.Fragment key={i}>
                    <div style={{ width : 'calc((100% - 550px) * ' + (i / (education.length - 1)) + ')' }} />
                    <GlassCard>
                        <div style={{ display : 'flex', justifyContent : 'space-between' }}>
                            <span>{item.name}</span>
                            <span>{item.date}</span>
                        </div>
                        <div className={'light-font'}>{item.field}</div>
                        <div className={'small-font'}>{item.location}</div>
                    </GlassCard>
                    <div
                        style={{ width : 'calc((100% - 550px) * ' + ((education.length - 1 - i) / (education.length - 1)) + ')' }} />
                </React.Fragment>
            })}
        </Section>
    );
}

