import React from 'react';
import { ReactComponent as SimpleHex } from '../assets/blank_hex.svg'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export const Hexagon = ( props ) => {
    const { blur = '0px', size : height, x : left, y : top, x_inv : right, y_inv : bottom, ...others } = props;
    
    return <div
        className={'hexagon'}
        style={{
            filter : 'blur(' + blur + ')',
            width : 'auto',
            height,
            top,
            left,
            right,
            bottom,
            ...others
        }}
    >
        <SimpleHex />
    </div>
}

export const HexagonIcon = ( { icon } ) => {
    return <div
        className={'hex-icon'}
    >
        <FontAwesomeIcon icon={icon} />
        <SimpleHex />
    </div>
}

export const HexagonNumber = ( { number } ) => {
    return <div className={'hex-number'}>
        <div className={'hex-stroke'}>
            <div>
                {number}
            </div>
        </div>
    </div>
}
