import React from 'react';
import { games } from "../../utils/Data";
import { GameCard } from "../Cards/GameCard";
import { Section } from "./Section";

export function GameSection( props ) {
    return (
        <Section {...props} >
            {games.map(( item, i ) => {
                return <GameCard
                    key={i}
                    link={item.link}
                    team_img={item.team_pic}
                    game_img={item.title_pic}
                />
            })}
        </Section>
    );
}
