import React from 'react';
import { Hexagon } from "./Hexagon";

const blurValue = 1;

export const ParallaxHexagons = ( { scrollY, hexes=[] } ) => (
    <div className={'parallax-hexagons'}>
        {hexes.map(( item, i ) => (
            <Hexagon
                key={i}
                {...item}
                y={item.y - (scrollY * item.z * 0.05)}
                // zIndex={0}
                blur={(item.z * blurValue) + 'px'}
                // fill={colors[i % colors.length]}
                fill={item.color}
            />
        ))}
    </div>
);


