import React  from 'react';
import { work } from "../../utils/Data";
import { WorkCard } from "../Cards/WorkCard";
import { Section } from "./Section";
import { WorkingContentSpace } from "../WorkingContentSpace";

export function WorkSection( props ) {
    const [selected, select] = React.useState(-1)
    
    function handleSelect(id) {
        if (selected === -1) {
            select(id)
            return
        }
        
        
        select(-1)
    }
    
    return (
        <Section {...props} >
                <div className={'border-element-container top-border'}>
                    <div className={'border-element'} />
                </div>
                
                <WorkingContentSpace>
                    {work.map(( item, i ) => (
                        <WorkCard
                            width={'100%'}
                            item={item}
                            key={i}
                            i={i}
                            selected={selected}
                            select={handleSelect}
                        />
                    ))}
                </WorkingContentSpace>
                
                <div className={'border-element-container bottom-border'}>
                    <div className={'border-element'} />
                </div>
        </Section>
    );
}
