import React from 'react';
import { ThemeButton } from "./ThemeButton";

export function Themed( { children } ) {
    const [ currentTheme, setCurrentTheme ] = React.useState(0)
    
    React.useEffect(() => {
        let d = new Date()
        let nextState = d.getHours() < 7 || d.getHours() > 17 ? 1 : 0;
        // setCurrentTheme(nextState)
        setCurrentTheme(1)
    }, [])
    
    return (
        <div className={themeClassName(currentTheme)}>
            {children.length?
                children.map(( item, i ) => React.cloneElement(item, { key : i, theme : themeName(currentTheme) }))
                :
                React.cloneElement(children, { theme : themeName(currentTheme) })
            }
            <ThemeButton
                currentTheme={currentTheme}
                changeThemeCallback={() => setCurrentTheme((currentTheme + 1)%2)}
            />
        </div>
    );
}

function themeClassName( currentTheme ) {
    return 'theme-div ' + themeName(currentTheme)
}

function themeName( currentTheme ) {
    return ([ 'light-theme', 'dark-theme' ][currentTheme])
}
