import React from 'react';
import { GatheringHexes } from "./GatheringHexes";
import { GrowingMainHex } from "./GrowingMainHex";
import { ShingLogo } from "./ShingLogo";
import { NamedLogo } from "./NamedLogo";
import { AnimateRibbon } from "./AnimateRibbon";

export function StartUpAnimation( { theme, callback } ) {
    const [ animationStep, setStep ] = React.useState(0)
    
    return (
        <div className={'start-up-anim' + (animationStep >= 5 ? ' hide' : '')}>
            {animationStep < 2 ?
                <GatheringHexes
                    animationLength={2}
                    hexCap={50}
                    hexTransitionTime={1}
                    theme={theme}
                    callback={() => setStep(1)}
                /> : <></>
            }
            
            {0 < animationStep && animationStep < 3 ?
                <GrowingMainHex
                    animationLength={0.5}
                    logoSize={300}
                    callback={() => setStep(2)}
                /> : <></>
            }
            
            {1 < animationStep && animationStep <= 3 ?
                <ShingLogo
                    animationLength={1}
                    logoSize={300}
                    callback={() => setStep(3)}
                /> : <></>
            }
            
            {2 < animationStep && animationStep < 4 ?
                <NamedLogo
                    animationLength={1}
                    logoSize={300}
                    callback={() => setStep(4)}
                /> : <></>
            }
            
            {3 < animationStep && animationStep < 6 ?
                <AnimateRibbon
                    animationLength={0.5}
                    logoSize={300}
                    callback={() => {
                        setStep(5)
                        setTimeout(() => setStep(6), 500)
                        callback()
                    }}
                /> : <></>
            }
        </div>
    );
}

