import React from 'react';
import { HexagonNumber } from "../Hexagon";

export function SectionTitle( { title, ref_var, number, startColor, endColor, isActive } ) {
    return (
        <div
            className={'section-title' + (isActive ? ' colored' : '')}
            style={{ '--startColor' : startColor, '--endColor' : endColor }}
        >
            {/*<div className={'section-number'}>*/}
            {/*    {number}*/}
            {/*</div>*/}
            <HexagonNumber
                number={number}
            />
            <h1 ref={ref_var}>
                {title}
            </h1>
        </div>
    );
}

