import React from 'react';
import { Hexagon } from "../Hexagon";

export function GrowingMainHex( { callback, logoSize, animationLength }) {
    const [ size, setSize ] = React.useState(0)
    
    React.useEffect(() => {
        setTimeout(() => setSize(logoSize), 100)
        setTimeout(() => callback(), animationLength*1000)
    }, [])
    
    return (
        <div className={'layer type-2'}>
            <Hexagon transition={animationLength+'s'} size={size + 'px'} x={'50%'} y={'50%'} />
        </div>
    );
}

