import React from 'react';
import { links, profile } from "../../utils/Data";
import { GlassCard } from "../Cards/GlassCard";
import { Section } from "./Section";
import { HexagonIcon } from "../Hexagon";

export function AboutSection( props ) {
    return (
        <Section {...props} >
            <GlassCard>
                <div className={'profile-description'}>
                    {profile.description}
                </div>
                <div className={'buttons'}>
                    {links.map(( item, i ) => (
                        <a
                            href={item.link}
                            target={'_newtab'}
                            className={'padded'}
                            key={i}
                        >
                            <HexagonIcon icon={item.icon} />
                            <div className={'text'}>
                                {item.name}
                            </div>
                        </a>
                    ))}
                </div>
            </GlassCard>
        
        </Section>
    );
}
