import React from 'react';
import './style/main.scss';
import { Themed } from "./components/Themed";
import { StartUpAnimation } from "./components/StartUpAnimation/StartUpAnimation";
import { doStartUp, genHexes, hexes, sections } from "./utils/Data";
import { Header } from "./components/Header";
import { WorkingContentSpace } from "./components/WorkingContentSpace";
import { HexagonIcon } from "./components/Hexagon";
import { ParallaxHexagons } from "./components/ParallaxHexagons";

export class App extends React.Component {
    
    state = {
        loaded : !doStartUp,
        windowInnerWidth : window.innerWidth,
        refs_built : false,
        activeSection : 0,
        scroll : 0
    }
    
    componentDidMount() {
        for ( let i = 0; i < sections.length; i++ ) {
            sections[i].ref_var = React.createRef();
        }
        window.addEventListener('scroll', this.handleScroll);
        this.setState({ refs_built : true })
    }
    
    scrollTo = ( e ) => {
        let yPos = e.current.getBoundingClientRect().y;
        window.scrollTo({
            top : window.scrollY + yPos - 100,
            left : 0,
            behavior : 'smooth'
        })
    }
    
    scrollToTop = () => window.scrollTo({
        top : 0, left : 0, behavior : 'smooth'
    })
    
    
    handleScroll = () => {
        let activeSection = -1;
        for ( let i = 0; i < sections.length; i++ ) {
            const refY = sections[i].ref_var.current.getBoundingClientRect().top
            if ( 50 < refY && refY < window.innerHeight * 0.75 ) {
                activeSection = i;
                break;
            }
        }
        if ( this.state.activeSection !== activeSection ) {
            this.setState({ activeSection })
        }
        this.setState({ scroll : window.scrollY })
    }
    
    render() {
        return <Themed>
            {doStartUp ?
                <StartUpAnimation callback={() => this.setState({ loaded : true })} /> : <></>
            }
            <Page
                activeSection={this.state.activeSection}
                loaded={this.state.loaded}
                scrollTo={this.scrollTo}
                toTop={this.scrollToTop}
                scrollY={this.state.scroll}
            />
        </Themed>
    }
}

const Page = ( { loaded, scrollTo, activeSection, theme, toTop, scrollY } ) => (
    <div className={'page'}>
        <Header scrollTo={scrollTo} theme={theme} />
        <PageBody display={loaded} activeSection={activeSection} toTop={toTop} scrollY={scrollY} />
    </div>
)

const PageBody = ( { display, activeSection, toTop, scrollY } ) => (
    <div className={'page-body' + (display ? ' unclip' : '')}>
        <ParallaxHexagons scrollY={scrollY} hexes={hexes} />
        <WorkingContentSpace>
            {sections.map(( item, i ) => (
                item.component({ key : i, isActive : i === activeSection, ...item })
            ))}
            <div className={'bottom-btn'}>
            <button className={'padded'} style={{ margin : 'auto' }} onClick={toTop}>
                <HexagonIcon
                    icon={'angle-double-up'}
                />
                <div className={'text'}>
                    Back to top
                </div>
            </button>
            </div>
        </WorkingContentSpace>
    </div>
)

