import React from 'react';
import { ReactComponent as Logo } from "../assets/grad.svg";
import { WorkingContentSpace } from "./WorkingContentSpace";
import { sections } from "../utils/Data";
import { HexagonIcon } from "./Hexagon";

export function Header( { scrollTo, theme } ) {
    
    return (
        <header>
            <WorkingContentSpace>
                <div className={'left-side'}>
                    <div/>
                    <div className={'logo'}>
                        <Logo />
                    </div>
                    <div className={'name'}>
                        Andrs Movilla
                    </div>
                </div>
                <div className={'right-side'}>
                    {sections.map(( item, i ) => (
                        <button
                            style={{ '--borderColor' : item.titleStartColor }}
                            onClick={() => scrollTo(item.ref_var)} key={i}
                        >
                            <HexagonIcon icon={item.icon} />
                            <div className={'text'}>
                                {item.name}
                            </div>
                        </button>
                    ))}
                </div>
            </WorkingContentSpace>
        </header>
    );
}

