import React from 'react';
import { SectionTitle } from "./SectionTitle";

export function Section( { ref_var, titleStartColor, titleEndColor, name, number, className, children, isActive } ) {
    
    return (
        <div className={'section ' + className}>
            <SectionTitle
                title={name}
                number={number}
                ref_var={ref_var}
                startColor={titleStartColor}
                endColor={titleEndColor}
                isActive={isActive}
            />
            
            <div className={'section-contents'}>
                {children}
            </div>
        </div>
    );
}

