import React from 'react';
import { ReactComponent as Logo } from '../../assets/grad.svg'

export function ShingLogo( { logoSize, callback, animationLength } ) {
    
    React.useEffect(() => {
        setTimeout(() => callback(), animationLength * 1000)
    }, [])
    
    return (
        <div className={'layer type-3'}>
            <div
                style={{ '--width' : logoSize + 'px', animationDuration : animationLength + 's' }}
                className={'full-logo'}
            >
                <Logo />
            </div>
        </div>
    );
}

