import React from 'react';
import { ReactComponent as Logo } from "../../assets/grad.svg";
import { basic } from "../../utils/Data";

export function NamedLogo( { logoSize, callback, animationLength } ) {
    
    React.useEffect(() => {
        setTimeout(() => callback(), 2100)
    }, [])
    
    return (
        <div className={'layer type-4'}>
            <div className={'names'} style={{ animationDuration : animationLength + 's' }}>
                <span className={'heavy'}>{basic.name}</span> <br />
                <span className={'light'}>{basic.slogan}</span>
            </div>
            <div style={{ '--width' : logoSize + 'px' }} className={'full-logo'}>
                <Logo />
            </div>
        </div>
    );
}

