import React from 'react';
import { Hexagon } from "../Hexagon";

export function GatheringHexes( { callback, hexTransitionTime,hexCap,animationLength } ) {
    const [ hexes, setHexes ] = React.useState([])
    const [ finished, setFinished ] = React.useState(false)
    let effectLimiter = Math.min(hexes.length, hexCap);
    
    React.useEffect(() => {
        if ( finished ) {
            return
        }
        const width = document.body.clientWidth;
        const height = document.body.clientHeight;
        
        function addHex() {
            
            function genDim( capX, capY, minSize, maxSize ) {
                let side = Math.random() * 4;
                let rand = side % 1;
                side -= rand;
                
                let y = ((side + 1) % 2) * (side >= 1 ? 1 : 0) * (capY - 150) + (side % 2) * rand * capY;
                let x = ((side + 1) % 2) * rand * capX + (side % 2) * (side <= 1 ? 0 : 1) * (capX - 150);
                
                x += 'px'
                y += 'px'
                return {
                    x, y, size : ((Math.random() * (maxSize - minSize)) + minSize) + 'px',
                    color : 'transparent',
                    transition : hexTransitionTime+'s'
                }
            }
            
            setHexes([ ...hexes, genDim(width + 300, height + 300, 75, 100) ])
        }
        
        if ( hexes.length > 0 ) {
            let h = [ ...hexes ];
            for ( const item of h ) {
                item.x = width / 2
                item.y = height / 2
                item.size = 40
                item.color = '#7100d3'
            }
            setHexes(h)
        }
        
        if ( hexes.length < hexCap ) {
            setTimeout(addHex, animationLength/(hexCap * 2))
        } else {
            setTimeout(() => {
                setFinished(true)
                callback()
            }, hexTransitionTime*1000)
        }
        
    }, [ effectLimiter, finished ])
    
    return (
        <div className={'layer type-1'}>
            {
                hexes.map(( item, i ) => <Hexagon key={i} {...item} />)
            }
        </div>
    );
}

