import React from 'react';
import { GlassCard } from "./GlassCard";
import { HexagonIcon } from "../Hexagon";

export function GameCard( { title, team_img, game_img, link } ) {
    return (
        <GlassCard
            title={title}
            innerClass={'img-style'}
        >
            <div className={'game-images'}>
                <div>
                    <img
                        src={team_img}
                        alt={'Team'}
                    />
                </div>
                <div>
                    <img
                        src={game_img}
                        alt={'Game'}
                    />
                </div>
            </div>
            <div className={'buttons'}>
                <a
                    className={'padded'}
                    href={link}
                    target={'_newtab'}
                >
                    <HexagonIcon icon={'hand-pointer'} />
                    <div className={'text'}>
                        Click here
                    </div>
                </a>
            </div>
        </GlassCard>
    );
}
